export default {
	data() {
		return {
			getCode: true,
			countDown: 60,
			gotoUrl: '',
			t1: '',
			phone: '',
			videoId: '',
		};
	},
	onLoad: function(option) {
		if (this.t1) {
			window.clearInterval(this.t1);
		}
		console.log(option, 123, this.t1)
		if (option.url) {
			this.gotoUrl = option.url;
		}
		if (option.videoId) {
			this.videoId = option.videoId;
		}
	},
	methods: {
		login(e) {
			let self = this;
			if (e.detail.value.nameValue.length !== 11) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
				})
				return;
			}
			uni.request({
				url: this.$serverUrl + '/user/login',
				data: {
					account: e.detail.value.nameValue,
					pwd: e.detail.value.passwordValue,
					type: 2,
					channelId: 0,
					deviceId: 0,
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				method: "POST",
				success: (res) => {
					if (res.data.code !== 0) {
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						})
						return;
					}

					uni.showToast({
						title: '登录成功',
						icon: 'none',
					})
					uni.setStorage({
						key: 'user_info',
						data: res.data.data,
						success: function() {
							console.log(self.gotoUrl, 111)
							if (self.t1) {
								window.clearInterval(self.t1);
							}
							if (self.gotoUrl) {
								if (self.videoId) {
									uni.redirectTo({
										url: '../../pages' + self.gotoUrl + '?id=' + self.videoId
									})
								} else {
									uni.navigateTo({
										url: '../../pages' + self.gotoUrl
									})
								}
							} else {
								uni.navigateTo({
									url: '../../pages/center/center'
								})
							}
						}
					})

				},
				fail: () => {
					uni.showModal({
						content: '请求失败，请重试!',
						showCancel: false
					})
				}
			})
			console.log("得到账号:" + e.detail.value.nameValue + ';得到密码:' + e.detail.value.passwordValue)
		},

		register() {
			if (this.t1) {
				window.clearInterval(this.t1);
			}
			// uni.request({
			// 	url: this.$serverUrl + '/user/login',
			// 	data: {
			// 		type: 1,
			// 		channelId: 0,
			// 		deviceId: 0,
			// 	},
			// 	header: {
			// 		"Content-Type": "application/x-www-form-urlencoded"
			// 	},
			// 	method: "POST",
			// 	success: (res) => {
			// 		if (res.data.code !== 0) {
			// 			uni.showModal({
			// 				content: '请求失败，失败原因：' + res.data.msg,
			// 				showCancel: false
			// 			})
			// 			return;
			// 		}
			if (this.gotoUrl) {
				if (this.gotoUrl == '/video/video_particulars') {
					uni.navigateBack({
						delta: 1
					})
					return;
				}
				uni.redirectTo({
					url: '../../pages' + this.gotoUrl
				})
			} else {
				uni.redirectTo({
					url: '../../pages/center/center'
				})
			}
			// 	},
			// 	fail: () => {
			// 		uni.showModal({
			// 			content: '请求失败，请重试!',
			// 			showCancel: false
			// 		})
			// 	}
			// })
		},

		onGetCode() {
			if (!this.getCode) {
				uni.showToast({
					title: '验证码已发送，若未收到请' + this.countDown + '秒后再试',
					icon: 'none'
				})
				return;
			}
			if (this.phone.length !== 11) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
				})
				return;
			}
			let self = this;
			uni.request({
				url: this.$serverUrl + '/user/sms',
				data: {
					phone: this.phone,
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				method: "POST",
				success: (res) => {
					if (res.data.code !== 0) {
						uni.showModal({
							content: '请求失败，失败原因：' + res.data.msg,
							showCancel: false
						})
						return;
					}

					self.getCode = false;
					self.t1 = setInterval(function() {
						self.countDown--;
						console.log(self.countDown)
						if (self.countDown <= 0) {
							self.getCode = true;
							self.countDown = 60;
							window.clearInterval(self.t1);
							return;
						}
					}, 1000)
				},
				fail: () => {
					uni.showModal({
						content: '请求失败，请重试!',
						showCancel: false
					})
				}
			})





		}
	}
}
